import express from 'express';
import expressJWT from 'express-jwt';
import env from '../../../config/env';
import getLoggedInUser from '../../../middlewares/get_loggedIn_user';
import addBreakages from '../controllers/breakages_post';
import { baseUrl } from '../baseurl';

const router = express.Router();

/**
 * @swagger
 * /breakages:
 *   post:
 *     tags:
 *       - Breakages
 *     description: Returns success message if breakage details are added
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "body"
 *         description: "Required details"
 *         required: true
 *         schema:
 *           type: object
 *           properties:
 *            productCode:
 *              type: string
 *            brandCode:
 *              type: string
 *            productName:
 *              type: string
 *            sizeCode:
 *              type: string
 *            date:
 *              type: date
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *            _id:
 *              type: string
 *            productCode:
 *              type: string
 *            brandCode:
 *              type: string
 *            productName:
 *              type: string
 *            sizeCode:
 *              type: string
 *            date:
 *              type: date
 */

router.post(
  `${baseUrl}/`,
  expressJWT({
    secret: env.JWT_SECRET,
  }),
  getLoggedInUser,
  addBreakages
);

export default router;
